.ba-contact {
  position: relative;
  overflow: hidden;
}

.ba-contact__main {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(159, 154, 207, 0.5);
}

.ba-contact__header {
  padding: 56px 10px 0;
}

.ba-contact__icon {
  background: url("../img/contact.png") 50% no-repeat;
  width: 42px;
  height: 60px;
}

.ba-contact__forms {
  max-width: 880px;
  margin: 0 auto;
  padding: 40px 10px 10px;
}

.ba-contact__input-wrap {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.ba-contact__input {
  border-radius: 4px 3px 3px 4px;
  background-color: rgba(235, 235, 235, 0.5);
  flex-grow: 1;
  outline: none;
  border: none;
  padding: 0 25px;
  margin-bottom: 30px;
  font-style: italic;
  color: #665f60;
}

.ba-contact__input:focus {
  background-color: #ebebeb;
}

.ba-contact__input-first-row {
  height: 56px;
}

.ba-contact__input-first-row:first-child {
  margin-right: 32px;
}

.ba-contact__input-text-area {
  width: 100%;
  max-height: 200px;
  padding-top: 25px;
}

.ba-contact__info {
  margin-top: 54px;
}

.ba-contact__info-block {
  background-color: rgba(255, 255, 255, 0.8);
  width: 348px;
  height: 91px;
  display: flex;
  position: relative;
  left: calc(100% - 91px);
  transition: 0.7s;
}

.ba-contact__info-block:hover {
  left: calc(100% - 348px);
}

.ba-contact__info-img {
  width: 91px;
  height: 91px;
  background-color: #9f9acf;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ba-contact__info-text {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.ba-contact__btn {
  display: block;
  width: 293px;
  height: 70px;
  border-radius: 4px;
  background-color: #9f9acf;
  color: #ebebeb;
  font-family: "Proxima Nova", sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  border: none;
  border-bottom: 4px solid #8783b0;
  text-align: center;
  vertical-align: middle;
  margin-right: auto;
  margin-left: auto;
}

.ba-contact__btn:hover {
  background-color: #33FF99;
  border: 4px solid #9f9acf;
}

::-webkit-input-placeholder {
  color: #fff;
}

::-moz-placeholder {
  color: #fff;
}

/* Firefox 19+ */
:-moz-placeholder {
  color: #fff;
}

/* Firefox 18- */
:-ms-input-placeholder {
  color: #fff;
}

#map {
  width: 100%;
  min-height: 900px;
}

.ba-scroll-up {
  width: 84px;
  height: 84px;
  position: absolute;
  right: 130px;
  bottom: 45px;
  display: block;
  transition: 0.25s;
  background: rgba(159, 154, 207, 0.5) url("../img/interface---arrow-top-.png") 50% no-repeat;
}

.ba-scroll-up:hover {
  background: #9f9acf url("../img/interface---arrow-top-.png") 50% no-repeat;
}

@media (max-width: 365px) {
  .ba-contact__info-block {
    width: 275px;
    font-size: 16px;
  }
  .ba-contact__info-block:hover {
    left: calc(100% - 270px);
  }
}

@media (max-width: 570px) {
  .ba-contact__input-wrap {
    flex-direction: column;
  }
  .ba-contact__input-first-row:first-child {
    margin-right: 0;
  }
  .ba-contact__btn {
    width: 230px;
  }
  #map {
    min-height: 1000px;
  }
}

@media (max-width: 840px) {
  .ba-scroll-up {
    right: 0;
  }
}

@media (max-width: 445px) {
  .ba-scroll-up {
    display: none;
  }
}
